module.exports = ".container {\n  display: grid;\n  grid-gap: 10px;\n  grid-template-columns: repeat(auto-fit, minmax(130px, 1fr));\n  grid-auto-rows: 150px;\n  grid-auto-flow: dense;\n}\n\n.parent {\n  width: 80%;\n  margin: 75px 10%;\n}\n\ndiv:focus {\n  outline: none;\n}\n\n.event-item {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  scroll-snap-align: start;\n  scroll-margin-top: 75px;\n  position: relative;\n  transition: transform 0.2s ease-in-out;\n}\n\n.event-title {\n  font-family: \"Metal Mania\", cursive;\n  position: absolute;\n  bottom: 10px;\n  left: 10px;\n  text-shadow: black 0px 0px 5px;\n  font-size: 1.4em;\n}\n\nh1 {\n  margin-top: 2em;\n  scroll-snap-align: start;\n  scroll-margin-top: 75px;\n}\n\n.event-img {\n  width: 100%;\n  height: 100%;\n  -o-object-fit: cover;\n     object-fit: cover;\n  cursor: pointer;\n}\n\n@media (max-width: 500px) {\n  .event-item {\n    border-width: 2px;\n    border-style: solid;\n    -o-border-image: linear-gradient(135deg, #ff2600, yellow, yellow, #ff2600) 1 round;\n       border-image: linear-gradient(135deg, #ff2600, yellow, yellow, #ff2600) 1 round;\n  }\n}\n\n.event-item {\n  transition: 0.5s color ease-in-out;\n}\n\n.event-item::before, .event-item::after {\n  box-sizing: inherit;\n  content: \"\";\n  position: absolute;\n  width: 100%;\n  height: 100%;\n}\n\n.event-item:hover {\n  color: gold;\n}\n\n.event-item::before, .event-item::after {\n  border: 2px solid transparent;\n  border-radius: 0px;\n  width: 0;\n  height: 0;\n  top: 0;\n  left: 0;\n}\n\n.event-item:hover::before, .event-item:hover::after {\n  width: 100%;\n  height: 100%;\n}\n\n.event-item ::before {\n  transition: width 0.25s ease-out, height 0.25s ease-out 0.25s;\n}\n\n.event-item:hover::before {\n  border-width: 2px;\n  border-style: solid;\n  -o-border-image: linear-gradient(135deg, #ff2600, yellow, yellow, #ff2600) 1 round;\n     border-image: linear-gradient(135deg, #ff2600, yellow, yellow, #ff2600) 1 round;\n  border-width: 2px 2px 0px 0px;\n  transition: width 0.25s ease-out, height 0.25s ease-out 0.25s, border-radius 0.6s;\n}\n\n.event-item:hover::after {\n  border-width: 2px;\n  border-style: solid;\n  -o-border-image: linear-gradient(135deg, #ff2600, yellow, yellow, #ff2600) 1 round;\n     border-image: linear-gradient(135deg, #ff2600, yellow, yellow, #ff2600) 1 round;\n  border-width: 0px 0px 2px 2px;\n  transition: height 0.25s ease-out, width 0.25s ease-out 0.25s;\n}\n\n.horizontal {\n  grid-column: span 2;\n}\n\n.vertical {\n  grid-row: span 2;\n}\n\n.big {\n  grid-column: span 2;\n  grid-row: span 2;\n}\n/*# sourceMappingURL=data:application/json;base64, */"